.statistics {
  width: 500px;
  margin: 0 auto;
}
.title {
  text-align: center;
  font-size: 30px;
}
.statList {
  display: flex;
  flex-direction: row;
  list-style: none;
  padding: 0;
}
.item:nth-child(1) {
  background-color: #fac4be;
}
.item:nth-child(2) {
  background-color: #fdfd9e;
}
.item:nth-child(3) {
  background-color: #d8bfd8;
}
.item:nth-child(4) {
  background-color: #cdff9a;
}
.item:nth-child(5) {
  background-color: #c2e0fd;
}
.item {
  display: flex;
  flex-direction: column;
  flex-basis: calc(100% / 5);
  margin: 5px;
}
.label,
.percentage {
  font-size: 18px;
  font-weight: 500;
  text-align: center;
}
